import React, { useEffect, useRef, useState } from 'react'
import Peer from 'peerjs'
import { useNavigate, useParams } from 'react-router-dom'

export const VideoChat = () => {
  const videoRef = useRef<HTMLVideoElement | null>(null)
  const peer = new Peer() // PeerJS nesnesi olutur

  useEffect(() => {
    navigator.mediaDevices
      .getUserMedia({ video: true, audio: true })
      .then((stream) => {
        if (videoRef.current) {
          videoRef.current.srcObject = stream
        }

        // Dier kullancyla balant kur
        peer.on('open', (peerId) => {
          console.log('My Peer ID:', peerId)

          // arlar dinle
          peer.on('call', (call) => {
            console.log('Incoming call')
            call.answer(stream) // Gelen ary yantla ve kendi medya akn gnder
            call.on('stream', (remoteStream) => {
              // Dier kullancnn akn alarak gster
              if (videoRef.current) {
                videoRef.current.srcObject = remoteStream
              }
            })
          })
        })
      })
      .catch((error) => {
        console.error('Error accessing media devices:', error)
      })
  }, [])

  return (
    <div>
      <video ref={videoRef} autoPlay muted style={{ width: '100%', maxWidth: '600px' }}></video>
    </div>
  )
}
